:root {
  --background: #87ceeb;
  --border: #2388b0;
  --word: #00274d;
}

body {
  margin: 0;
}

.link {
  background-color: var(--word);
  border: 0rem var(--border) solid;
  z-index: 9997;
  border-left: 0;
  flex-direction: column;
  width: 18vw;
  padding-top: 8vh;
  padding-bottom: 5rem;
  font-size: xx-large;
  display: flex;
}

.link a {
  color: #f0f8ff;
  z-index: 9998;
  background-color: #0000;
  border-bottom: 1px solid #555;
  padding-top: .5rem;
  padding-left: 1vw;
  padding-right: 2vw;
  text-decoration: none;
}

.link a:hover {
  z-index: 9998;
  background-color: #2388b0;
}

.three_lines {
  background-color: var(--background);
  color: #fff;
  z-index: 9999;
  border: none;
  border: var(--border);
  border-radius: 20px;
  margin-top: 1rem;
  margin-left: 1rem;
  padding: 3vh;
  font-size: 100%;
  position: fixed;
  top: 0;
}

.three_lines:hover {
  background-color: #87cfebc8;
}

.sidebar {
  z-index: 1001;
  height: 100%;
  transition: left .75s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: -18vw;
}

.peoplesocialicon ul li {
  margin: 0 .75rem;
  list-style: none;
}

.peoplesocialicon ul li a {
  align-items: center;
  display: flex;
}

.intur a {
  color: #585858;
}

.peoplesocialicon ul {
  flex-flow: row;
  align-items: center;
  margin: .25rem 0;
  padding: 0 0 0 1.5rem;
  display: flex;
}

.peoplesocialicon {
  border: .1rem solid #4d0c00;
  border-radius: 3rem;
  margin-top: 1rem;
}

.peopleintrotitle {
  text-align: center;
  min-height: 80vh;
  margin: auto;
}

.peopleintrobg {
  filter: blur(5px);
  background-image: url("allotgetherphoto.5a2109f6.jpg");
  background-size: cover;
  height: 80vh;
}

.peopleintrobox {
  z-index: 2;
  background: #0006;
  border: .5rem solid #f0f8ff;
  min-width: 50%;
  min-height: 30%;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.peopleintrobox h1 {
  color: #f0f8ff;
  font-size: 3rem;
}

.peopleintrooutbox {
  z-index: 100;
  background: none;
}

.first {
  text-align: center;
  border-radius: 1rem;
  width: 60vw;
  height: auto;
}

.first p {
  width: 60vw;
  margin: auto;
}

#front {
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
}

.first {
  margin: auto;
}

.int {
  padding-bottom: 5vh;
}

.down {
  text-align: center;
  background-color: #fff;
  border: none;
  border-radius: 1rem;
  width: 50vw;
  height: 10vh;
  margin: auto;
  display: block;
}

div.arrow {
  box-sizing: border-box;
  width: 6vmin;
  height: 6vmin;
  margin: auto;
  display: block;
  transform: rotate(135deg);
}

div.arrow:before {
  content: "";
  transform-origin: 100% 0;
  border: .8vmin solid #fafafa;
  border-width: .8vmin .8vmin 0 0;
  width: 100%;
  height: 100%;
  transition: all .2s;
  display: block;
}

div.arrow:after {
  content: "";
  float: left;
  transform-origin: 100% 0;
  border: 0 solid #fafafa;
  border-right-width: .8vmin;
  width: 100%;
  height: 100%;
  transition: all .2s;
  position: relative;
  top: -100%;
}

div.arrow:hover:after {
  border-color: #8dc4e2;
  height: 120%;
  transform: rotate(45deg);
}

div.arrow:hover:before {
  border-color: #8dc4e2;
  transform: scale(.8);
}

.history p {
  text-align: left;
  margin: 0;
  padding-bottom: 1vh;
}

.history p.dimmed {
  opacity: .3;
}

.history p:hover {
  opacity: 1;
}

.flex {
  display: flex;
}

.line {
  border: var(--border) solid;
  margin: 0 0 3vh;
  padding: 0;
}

.leader {
  color: var(--word);
  margin: 0 auto 0 0;
  padding-bottom: 1vh;
  font-size: 3vh;
}

.intur h1 {
  margin-bottom: 0%;
  font-size: 10vh;
}

.leim {
  width: auto;
  height: 70vh;
}

