nav {
  color: #f0f8ff;
  cursor: default;
  z-index: 1000;
  background-color: #00274d;
  flex-wrap: nowrap;
  width: 100%;
  max-height: 8vh;
  position: fixed;
  top: 0;
}

nav ul {
  flex-flow: row;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  margin: auto;
  display: flex;
}

nav ul li {
  padding: .75rem 0;
  list-style: none;
}

nav ul li a {
  color: #f0f8ff;
  padding: .75rem 1rem;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

nav ul li a.active {
  color: gold;
  text-shadow: 0 0 10px gold, 0 0 20px gold;
  font-weight: 700;
}

.c-compare {
  --h: 9;
  --m: .2rem 0;
  --w: 16;
  --thumb-bgc: var(--border);
  --thumb-bgc-focus: transparent;
  --thumb-w: 1rem;
  max-width: 60vw;
  margin: var(--m);
  border-radius: 1rem;
  margin: auto;
  position: relative;
}

.c-compare:after {
  content: "";
  padding-bottom: calc((var(--h) / var(--w)) * 100%);
  display: block;
}

.c-compare__left, .c-compare__right {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.c-compare__left {
  clip-path: polygon(0% 0%, var(--value) 0%, var(--value) 100%, 0% 100%);
}

.c-compare__right {
  clip-path: polygon(100% 0%, var(--value) 0%, var(--value) 100%, 100% 100%);
}

.c-compare__range {
  box-sizing: block;
  background-color: #0000;
  border: 0 #0000;
  outline: none;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: inherit;
  position: absolute;
  top: 0;
}

.c-compare__range::-moz-range-thumb {
  cursor: ew-resize;
  height: 100%;
  width: var(--thumb-w);
  background-color: #0000;
}

.c-compare__range::-webkit-slider-thumb {
  cursor: ew-resize;
  height: 100%;
  width: var(--thumb-w);
  background-color: #0000;
}

.c-compare__range:focus::-webkit-slider-thumb {
  background-color: #0000;
  box-shadow: 0 0 0 3px #0000;
}

.c-compare__range:focus::-moz-range-thumb {
  background-color: #0000;
  box-shadow: 0 0 0 3px #0000;
}

.c-compare__range::-moz-range-track {
  box-sizing: border-box;
  background: 0 0 / 100%;
}

.c-compare__range::-webkit-slider-runnable-track {
  box-sizing: border-box;
  background: 0 0 / 100%;
  height: 100%;
}

.c-compare__range {
  appearance: none;
}

.c-compare__range::-webkit-slider-runnable-track {
  appearance: none;
}

.c-compare__range::-webkit-slider-thumb {
  appearance: none;
}

.c-compare img {
  border-radius: 1rem;
}

.clti {
  color: var(--word);
}

.g {
  text-align: center;
  background-color: #a7e6ff;
  width: 30vw;
}

.claint {
  color: var(--word);
  padding-left: 1vw;
  padding-right: 1vw;
  font-weight: 600;
}

#intro {
  display: flex;
}

.m {
  width: 100%;
  height: 100%;
}

.goalll {
  width: 60vw;
  margin-left: 15vw;
}

@media only screen and (width <= 600px) {
  body {
    background-color: #add8e6;
  }

  .sidebar {
    --background: #87ceeb;
    --border: #2388b0;
    --word: #00274d;
    transition: left .75 ease-in-out;
    z-index: 1001;
    height: 100%;
    display: flex;
    position: fixed;
    top: 0;
    left: -100vw;
  }

  .link {
    width: 100vw;
  }

  #intro {
    flex-flow: column-reverse wrap;
  }

  #history {
    flex-flow: column wrap;
  }

  .introd, .m {
    height: auto;
  }

  .g {
    width: 90%;
    height: auto;
  }

  .first {
    width: 90vw;
  }

  .first h2 {
    font-size: 5vw;
  }

  .shhe {
    width: auto;
    height: 60vw;
    margin: auto;
  }

  .his {
    width: auto;
    height: 90vw;
    margin-left: auto;
    margin-right: 0%;
  }

  .history p {
    padding-bottom: 0;
  }

  .line {
    margin-bottom: 0%;
  }

  .goalll {
    width: 90vw;
    margin: auto;
  }
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
